/**
 *API
 */
import axios from '@/libs/api.request'
import config from '@/config'
//import Qs from 'qs'
 let apiPath= '/clerp-app-api'  // 
/**
 * @description 
 * @params {url} 
 */
export const getNoticeInfo = ({url}) => {
	//
	let data = {
		isLoad:'false',
	}

	
	return axios.request({
		url: `${apiPath}${url}`,
		data,
		method: 'POST',
	})
}

