/**
 * @description 
 * @params {}
 */
export const boxDetailApproval = ({
	Id,
	approvalExplain,
	approveState
}) => {
	//
	let data = {
		ID1:Id,
		bi_SPriceAudit:approveState,	//1:0:
		bi_SPriceAuditExplain:approvalExplain	//
	}

	return axios.request({
		url: `${apiPath}/confirm/priceAudit/priceAudit`,
		data,
		method: 'POST',
	})
}

